import uniTag from '@dcloudio/uni-ui/lib/uni-tag/uni-tag.vue';
import redirect from '../../mixins/redirect.js';

export default {
	components: {
		uniTag
	},
	mixins: [redirect],
	data() {
		return {
			history: uni.getStorageSync('history') ? JSON.parse(uni.getStorageSync('history')) : [],
			hot: [],
			keyword: '',
			showActionSheet: false,
			tips: '确认清空搜索历史吗？'
		};
	},
	mounted() {
		this.getHos();
	},
	methods: {
		cleanKeyword() {
			this.keyword = '';
		},
    clearHistory(){
		  this.history = []
      uni.removeStorageSync('history');
    },
		getShopGoods() {
			const arr = uni.getStorageSync('history');
			const history = arr ? JSON.parse(arr) : [];
			if (history.indexOf(this.keyword) === -1) {
				history.unshift(this.keyword);
				if (history.length >= 10) {
					history.splice(history.length - 1, 1);
				}
				uni.setStorageSync('history', JSON.stringify(history));
			}
      this.toQueryLink('/pages/goods/lists', { keyword: this.keyword });
		},
		toGoodsLists(item) {
			console.log(item);
			this.keyword = item;
			this.toQueryLink('/pages/goods/lists', { keyword: this.keyword });
		},
		getHos() {
			this.$http({
				aurl: 'hotKeyWord',
				success: res => {
					let hotArr = res.data.result.data
					if (hotArr.length > 0) {
						this.hot = hotArr;
						console.log(hotArr)
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		}
	}
};
